<template>
    <div>
        <!--used to add work into the To Do List-->
        <form @submit="addWork">
            <!--v-model used to create two way data bindings on data input-->
            <input type="text" v-model="title" name="title" placeholder="Add Work To List">
            <input type="submit" value="Add Work">
        </form>
    </div>
</template>

<script>
// uuid used to make unique ids
import uuid from 'uuid';
export default {
    name: "AddWork",
    data(){
        return{
            title: ''
        }
    },
    methods:{
        addWork(e){
            e.preventDefault();
            const work ={
                id: uuid.v4(),
                title: this.title,
                completed: false
            };
            this.$emit('add-work', work);
        }
    }
}
</script>

<style scoped>
/* designs*/
    input[type="text"]{
        background: #2c3539;
        width: 300px;
        height: 25px;
        color: red;
    }
    input[type="submit"]{
        display:block;
        
        margin:auto;
        background: red;
        color: white;
        width:200px;
        margin-top: 15px;
        margin-bottom: 15px;
    }
</style>