<template>
    <div>
        <!-- binds the work into the to do list-->
        <p class="works">Pending Work:</p>
        <div v-bind:key="work.id" v-for="work in pending">
        <Work v-bind:work="work" v-on:del-work="$emit('del-work', work.id)" />
        </div>
        <p class="works">Completed Work:</p>
        <div v-bind:key="work.id" v-for="work in completed">
        <Work v-bind:work="work" v-on:del-work="$emit('del-work', work.id)" />
        </div>    
    </div>
</template>

<script>
import Work from './Work';
export default {
    components: {
        Work
    },
    name: "ToDoList",
    props:["works"],
    computed:{
    pending: function() {
      return this.works.filter(function(work) {
        return work.completed === false;
      })
    },
    completed: function() {
      return this.works.filter(function(work) {
        return work.completed === true;
      });
    }
}
}
</script>

