<template>
<!-- used to bind the work produced from addwork into todolist-->
    <div class="work-item" v-bind:class="{'is-complete':work.completed}"
    contenteditable="true"
    v-on:keydown.enter="updateWork('update-work',work.title)"
    v-on:blur="updateWork('update-work', work.title)">
        <!--cross todo list-->
        <input type="checkbox" v-on:change="markComplete(work.title)">
        {{work.title}}
        <!-- delete todo list-->
    <button @click="$emit('del-work', work.id)">Delete Work</button>
    </div>
</template>
<script>
export default {
    name: "Work",
    props: ["work"],
    methods: {
        markComplete(){
            this.work.completed = !this.work.completed;
        },
        updateWork(e,work){
            e.preventDefault();
            work.title = e.target.innerText;
            e.target.blur();
        }
        }
    }
</script>
<style scoped>
/*style*/
    .work-item{
        padding: 10px;
    }

    .is-complete{
        text-decoration: line-through;
        color:white;
    }
    button{
        background:orange;
        color:white;
    }
</style>