<template>
  <div id="app">
    <!--Header-->
    <header class="header">
      <h1> To Do List</h1>
    </header>
    <!--Add Work Button and textBox-->
    <div class="addwork">
      <h2> Add Work </h2>
    <AddWork v-on:add-work="addWork" />
    </div>
    <div class="task">
      <h2> To Do </h2>
      <!-- Add Task, Todo list and the delete work button for each task-->
    <ToDoList v-bind:works="works" v-on:del-work="deleteWork"/>
    </div>
  </div>
</template>

<script>
import AddWork from './components/AddWork';
import ToDoList from './components/ToDoList';

export default {
  name: 'App',
  components: {
    ToDoList,
    AddWork
  },
  data(){
    return{
      //work in arraylist
      works:[]
    }
  },
  //functions
  methods:{
    deleteWork(id){
      this.works = this.works.filter(work => work.id !== id);
    },
    addWork(work){
      this.works.push(work);
    }
  }
}
</script>

